.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.navbar {
  display: flex;
  background-color: rgba(255, 255, 79, 0.712);
  width: 100%;
  height: 75px;
}

.left-navbar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex: 0 0 400px;
  cursor: pointer;
}

.left-navbar img {
  height: 55%;
}

.mid-navbar {
  display: flex;
  flex: 1;
}

.right-navbar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex: 0 0 400px;
  cursor: pointer;
}

.right-navbar a {
  text-decoration: none;
}

.right-navbar button {
  height: 45px;
  border-radius: 10%;
  cursor: pointer;
  background-color: rgb(226, 255, 182);
}

.about-me {
  display: flex;
  justify-content: center;
  height: 300px;
}

.about-me-space {
  width: 80px;
}

.about-me img {
  height: 100%;
  align-self: center;
}

.biodata {
  width: 400px;
}

.favourites {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  height: 400px;
}

.hobbies {
  background-color: rgb(255, 86, 86);
}

.hobbies-fill, .movies-fill {
  display: flex;
  justify-content: space-evenly;
}

.hobbies img, .fav-movies img, .fav-foods img {
  height: 100px;
}

.fav-movies {
  background-color: rgb(136, 136, 255);
}

.fav-foods {
  background-color: rgb(115, 255, 115);
  color: 
}

.portofolios {
  display: inline-grid;
  grid-template-columns: auto auto auto auto auto;
  column-gap: 20px;
}

.portofolios img {
  height: 150px;
  width: 200px;
}

.message-section {
  background-color: rgba(255, 255, 79, 0.712);
}

.message form{
  display: flex;
  justify-content: space-around;
  margin-top: -30px;
}

.message form input {
  margin-top: -30px;
  height: 30px;
  width: 200px;
}

.message form h5 {
  margin-bottom: 5px;
  align-self: left;
}

.footer {
  display: flex;
  height: 100px;
  background-color: rgba(255, 255, 79, 0.712);
}

.left-footer {
  width: 65%;
}

.right-footer {
  width: 35%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.right-footer img {
  height: 30px;
  width: 30px;
}

@media only screen and (max-width: 815px) {
  .right-navbar a {
    display: none;
  }
  .right-navbar {
    flex: 0 0 auto;
    justify-content: flex-end;
  }
  .right-navbar button {
    align-self: right;
  }
}

@media only screen and (max-width: 1000px) {
  .favourites {
    height: 800px;
    grid-template-columns: auto auto;
    grid-template-rows: 400px 400px;
  }

  .footer {
    justify-content: center;
  }
}

@media only screen and (max-width: 875px) {
  .portofolios {
    grid-template-columns: auto auto auto;
  }

  .message form{
    flex-direction: column;
  }

  .message form input {
    width: 600px;
  }
}

@media only screen and (max-width: 665px) {
  .about-me {
    height: 700px;
    flex-direction: column;
    align-items: center;
  }

  .biodata {
    justify-self: center;
  }

  .about-me img {
    height: 400px;
  }

  .favourites {
    height: 1200px;
    grid-template-columns: auto;
    grid-template-rows: 400px 400px 400px;
  }

  .portofolios {
    grid-template-columns: auto auto;
  }

  .message form{
    flex-direction: column;
  }

  .message form input {
    width: 400px;
  }

  .footer {
    justify-content: center;
  }

  .left-footer {
    width: 0%;
  }

  .right-footer {
    width: 100%;
  }
}

@media only screen and (max-width: 500px) {
  .left-navbar img {
    height: 45%;
  }

  .left-navbar h1 {
    font-size: 25px;
  }

  .left-navbar {
    flex: 0 0 300px;
  }

  .right-navbar button {
    margin-right: 5px;
  }

  .right-navbar {
    flex: 0 0 100px;
  }

  .portofolios img {
    width: 180px;
    height: 130px;
  }

  .message form input {
    width: 300px;
  }
}
